import React from 'react';
import logo, { ReactComponent } from './logo.svg';
import './App.css';
import ListItems from './ListItems';



class App extends React.Component {
  constructor(props){
    super(props);

    this.state={
      items: [],
      currentItem: {
        text:'',
        key:''
      }
    }

    this.handleInput= this.handleInput.bind(this);
    this.addItem= this.addItem.bind(this);
    this.deleteItem= this.deleteItem.bind(this);
  }

  handleInput(e){
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now()
      }
    })
  }

  addItem(e){
    e.preventDefault();
    const newItem = this.state.currentItem;
    if(newItem.text!==""){
      const newItems = [...this.state.items,newItem]
      //console.log(newItems);
      this.setState({
        items: newItems,
        currentItem: {
          text:'',
          key:''
        }
      })
    }
  }

  deleteItem(key)
  {
    const filteredItem = this.state.items.filter(item => item.key!==key);
    this.setState({
      items: filteredItem
    })
  }
  
  render() {
    return (
      <div className="App">
        <header>
          <form id="to-do-list" onSubmit={this.addItem}>
            <input type="text" placeholder="Enter text" value={this.state.currentItem.text} 
            onChange={this.handleInput}/>
            <button type="submit"> Add </button> 
          </form>
        </header>
        <ListItems items={this.state.items} deleteItem={this.deleteItem}> </ListItems>
      </div>
    )
  }
}

export default App;
